import MaterialTable from "material-table"
import React, {useState,useEffect} from "react"

export const Table1 = () =>{

    const [data, setData] = useState([])

    const columns = [
        {
            field: 'avatar',
            title: 'Avatar',
            render: rowData => <img src={rowData.avatar} style={{width: 50, borderRadius: '50%'}} alt="user_avatar"/>
          },
        {title:"Rank", field: "rank"},
        {title:"Username", field: "username"},
        {title:"Score", field: "score"},
    ]

    useEffect(()=>{
        fetch("https://api.chess.com/pub/leaderboards")

        .then((resp)=>resp.json())
        .then((resp)=>{
            setData(resp.live_blitz)
            console.log(resp.live_blitz)
            
        })
    },[])



    return(
        <div className="size">
    <MaterialTable
        title="Chess.com Blitz Ranking"
        data={data}
        columns={columns}
        options={{
          search: false,
          paging: true,
          filtering: true,
          exportButton: true,
        //   actionsColumnIndex: -1,
          maxBodyHeight: "30em",
          pageSize: 10,
          pageSizeOptions: [10, 25, 50],
        }}

        
        

        // localization={{
        //   body: {
        //     editRow: {
        //       deleteText: "Â¿Esta seguro que desea eliminar a este usuario?",
        //     },
        //   },
        // }}

      />
        </div>
    )
}